html,
body {
	width: 100vw;
	height: 100vh;
	padding: 0;
	margin: 0;
	display: flex;
	align-items: center;
	justify-content: center;
}
body {
	font-family: 'Prompt', sans-serif;
	background: linear-gradient(
		top,
		#ff3232 0%,
		#fcf528 16%,
		#28fc28 32%,
		#28fcf8 50%,
		#272ef9 66%,
		#ff28fb 82%,
		#ff3232 100%
	);

	/* Firefox */
	background: -moz-linear-gradient(
		top,
		#ff3232 0%,
		#fcf528 16%,
		#28fc28 32%,
		#28fcf8 50%,
		#272ef9 66%,
		#ff28fb 82%,
		#ff3232 100%
	);

	/* Chrome,Safari4+ */
	background: -webkit-gradient(
		linear,
		left top,
		left bottom,
		color-stop(0%, #ff3232),
		color-stop(16%, #fcf528),
		color-stop(32%, #28fc28),
		color-stop(50%, #28fcf8),
		color-stop(66%, #272ef9),
		color-stop(82%, #ff28fb),
		color-stop(100%, #ff3232)
	);

	/* Chrome10+,Safari5.1+ */
	background: -webkit-linear-gradient(
		top,
		#ff3232 0%,
		#fcf528 16%,
		#28fc28 32%,
		#28fcf8 50%,
		#272ef9 66%,
		#ff28fb 82%,
		#ff3232 100%
	);

	background-size: 1000%;
	-moz-background-size: 1000%;
	-webkit-background-size: 1000%;

	/* W3C */
	animation-name: fun-time-awesome;
	animation-duration: 40s;
	animation-timing-function: linear;
	animation-iteration-count: infinite;
	animation-direction: alternate;
	animation-play-state: running;

	/* Firefox: */
	-moz-animation-name: fun-time-awesome;
	-moz-animation-duration: 40s;
	-moz-animation-timing-function: linear;
	-moz-animation-iteration-count: infinite;
	-moz-animation-direction: alternate;
	-moz-animation-play-state: running;

	/* Chrome, Safari */
	-webkit-animation-name: fun-time-awesome;
	-webkit-animation-duration: 40s;
	-webkit-animation-timing-function: linear;
	-webkit-animation-iteration-count: infinite;
	-webkit-animation-direction: alternate;
	-webkit-animation-play-state: running;
}

/* W3C */
@keyframes fun-time-awesome {
	0% {
		background-position: left top;
	}
	100% {
		background-position: left bottom;
	}
}

/* Firefox */
@-moz-keyframes fun-time-awesome {
	0% {
		background-position: left top;
	}
	100% {
		background-position: left bottom;
	}
}

/* Chrome, Safari */
@-webkit-keyframes fun-time-awesome {
	0% {
		background-position: left top;
	}
	100% {
		background-position: left bottom;
	}
}

.name {
	font-weight: 700;
	font-size: 12vh;
}

.number {
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 12vh;
	background-color: white;
	width: 14vh;
	height: 12vw;
}

.number {
	cursor: pointer;
}

.number_container {
	display: flex;
	justify-content: center;
	justify-content: space-between;
	width: 100%;
}

.footer {
	position: fixed;
	left: 0;
	bottom: 20px;
	width: 100%;
	/* background-color: red; */
	color: white;
	text-align: center;
}
